@charset "utf-8";

.page {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: Inter, Arial, sans-serif;
  max-width: 1600px;
  min-height: 100vh;
  min-width: 300px;
  position: relative;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.page_align_center {
  margin: 0 auto;
}

.content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
}

.section-title {
  width: 100%;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: -0.04em;
  color: #fff;
  margin: 0;
  padding-bottom: 70px;
}

@media (max-width: 400px) {
  .section-title {
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 60px;
  }
}

.section-title::after {
  content: "";
  border-bottom: 1px solid #DADADA;
  display: block;
  width: 100%;
  padding-top: 23px;
}

@media (max-width: 400px) {
  .section-title::after {
    padding-top: 28px;
  }
}

@media (min-width: 401px) {
  .section-title_type_about-me {
    padding-bottom: 66px;
  }
}

.display-none {
  display: none;
}

.entry {
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  justify-content: center;
  align-items: center;
  padding-top: 70px;
  padding-bottom: 70px;
  width: 100%;
  background-color: #202020;
}

@media (max-width: 400px) {
  .entry {
    padding: 56px 0px;
    justify-content: center;
  }
}

.entry_type_profile {
  /* max-width: 410px; */
}

/* @media (max-width: 800px) {
  .entry_type_profile {
    margin: 30px;
  }
} */

@media (max-width: 400px) {
  .entry_type_profile {
    margin: unset;
  }
}

@media (max-width: 800px) {
  .entry {
    padding-top: 232px;
  }
}

.entry__title {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  color: #fff;
  margin: 40px 0;
}

@media (max-width: 400px) {
  .entry__title {
    margin: 50px 0 80px;
  }
}

.entry__title_type_profile {
  margin: 4px auto 108px;
}

@media (max-width: 800px) {
  .entry__title_type_profile {
    margin-bottom: 80px;
  }
}

@media (max-width: 400px) {
  .entry__title_type_profile {
    margin: 14px auto 64px;
  }
}

.entry__form {
  /* width: 100%; */
  width: 396px;
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (max-width: 400px) {
  .entry__form {
    width: 260px;
  }
}
.entry__field {
  display: flex;
  flex-direction: column;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  color: #8B8B8B;
  position: relative;
}

.entry__field_type_profile {
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  font-size: 11px;
  line-height: 13px;
  color: #fff;
}

.entry__field_type_profile:first-child::after {
  content: "";
  border-bottom: 1px solid #424242;
  display: block;
  width: 100%;
}

.entry__input {
  border: none;
  min-height: 46px;
  background: #202020;
  border-bottom: 1px solid #424242;
  margin: 5px 0 10px;
  padding: 0 15px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #fff;
}

.entry__input:focus {
  outline-color: #f3c1f8;
  background: #fff;
}

.entry__input_type_error {
  color: #ff4062;
}

.entry__input_type_profile {
  text-align: right;
  flex: 1;
  background: unset;
  margin: 0;
  font-size: 11px;
  line-height: 13px;
  padding: 0;
  margin-left: 30px;
}

.entry__input-error {
  position: absolute;
  top: 78px;
  left: 50px;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  color: #ff4062;
  z-index: 1;
}

.entry__input-error_type_profile {
  top: 32px;
  left: 65px;
}

.entry__button-submit {
  margin: 70px 0 16px;
  width: 100%;
  border: none;
  background: #4285F4;
  border-radius: 3px;
  min-height: 45px;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  transition: transform 0.7s, opacity.5s;
}

@media (max-width: 400px) {
  .entry__button-submit {
    margin: 159px 0 11px;
    font-size: 12px;
    line-height: 15px;
  }
}

.entry__button-submit:hover {
  cursor: pointer;
  opacity: 0.7;
  transform: scale(1.05);
}

.entry__button-submit:active:hover {
  opacity: 0.8;
  transform: scale(1);
  transition: transform 0.1s;
}

.entry__button-submit_type_disabled {
  background: rgb(223, 222, 222);
  color: #cacaca;
}

.entry__button-submit_type_disabled:hover {
  opacity: 1;
  transform: none;
  cursor: auto;
}

.entry__button-submit_type_profile-disabled {
  background: rgb(223, 222, 222);
  color: #fff;
  opacity: 0.4;
}

.entry__button-submit_type_profile-disabled:hover {
  transform: none;
  opacity: 0.4;
  cursor: auto;
}

.entry__button-submit_type_disabled:hover::after {
  content: "Нужно заполнить все поля формы.";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 70%;
  color: #ff4062;
}

.entry__button-submit_type_profile {
  background: transparent;
  font-weight: normal;
  font-size: 13px;
  line-height: 16px;
  color: #fff;
  min-height: unset;
  margin-top: 202px;
}

@media (max-width: 800px) {
  .entry__button-submit_type_profile {
    margin-top: 193px;
  }
}

@media (max-width: 400px) {
  .entry__button-submit_type_profile {
    margin-top: 363px;
    font-size: 12px;
    line-height: 15px;
    margin-bottom: 15px;
  }
}

.entry__button-submit_type_sign-in {
  margin-top: 159px;
}

@media (max-width: 400px) {
  .entry__button-submit_type_sign-in {
    margin-top: 247px;
  }
}

.entry__text {
  display: inline-block;
  width: 100%;
  text-align: center;
  margin: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #8B8B8B;
}

@media (max-width: 400px) {
  .entry__text {
    font-size: 12px;
    line-height: 15px;
  }
}

.entry__link {
  text-decoration: none;
  display: inline-block;
  transition: opacity 0.5s, transform 0.5s;
  margin-left: 3px;
  color: #4285F4;
}

.entry__link:hover {
  cursor: pointer;
  opacity: 0.7;
  transform: scale(1.05);
}

.entry__button-sign-out {
  font-size: 13px;
  line-height: 16px;
  color: #EE3465;
  margin: 0 auto;
  border: none;
  background: transparent;
  transition: opacity 0.5s, transform 0.5s;
}

.entry__button-sign-out:disabled {
  color: #8B8B8B;
}

.entry__button-sign-out:disabled:hover {
  cursor: auto;
  opacity: 1;
  transform: unset;
}

@media (max-width: 400px) {
  .entry__button-sign-out {
    font-size: 12px;
    line-height: 15px;
  }
}

.entry__button-sign-out:hover {
  cursor: pointer;
  opacity: 0.7;
  transform: scale(1.05);
}

.entry__submit-message {
  position: absolute;
  bottom: 25%;
  left: 50%;
  transform: translateX(-50%);
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
}

@media (max-width: 400px) {
  .entry__submit-message {
    font-size: 12px;
    line-height: 15px;
  }
}

.entry__submit-message_active {
  opacity: 1;
  visibility: visible;
  transition: opacity 1s;
}

.entry__submit-message_type_error {
  color: red;
}
