.techs {
    padding: 100px 70px 95px;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    background: #272727;
  }
  
  @media (max-width: 800px) {
    .technology {
      padding: 90px 50px 85px;
    }
  }
  
  @media (max-width: 400px) {
    .technology {
      padding: 70px 18px 65px;
      background: #202020;
    }
  }
  
  .techs__title {
    font-style: normal;
    font-weight: normal;
    font-size: 50px;
    line-height: 58px;
    text-align: center;
    letter-spacing: -0.04em;
    color: #fff;
    width: 100%;
    margin: 20px 0 26px;
  }
  
  @media (max-width: 800px) {
    .technology__title {
      margin: 10px 0 22px;
    }
  }
  
  @media (max-width: 400px) {
    .technology__title {
      font-size: 30px;
      line-height: 36px;
      margin: 0 0 24px;
    }
  }
  
  .techs__subtitle {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    letter-spacing: -0.04em;
    color: #fff;
    max-width: 460px;
    margin: 0 auto 95px;
  }
  
  @media (max-width: 800px) {
    .technology__subtitle {
      font-size: 12px;
      line-height: 18px;
      margin-bottom: 78px;
    }
  }
  
  @media (max-width: 400px) {
    .technology__subtitle {
      font-size: 11px;
      line-height: 16px;
      margin-bottom: 45px;
    }
  }
  
  .techs__items {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  @media (max-width: 400px) {
    .technology__items {
      max-width: 188px;
      margin: 0 auto;
      justify-content: unset;
    }
  }
  
  .techs__item {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    letter-spacing: -0.04em;
    color: #fff;
    background: #303030;
    border-radius: 10px;
    min-width: 90px;
    min-height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px;
    padding: 0 10px;
    box-sizing: border-box;
  }
  
  @media (max-width: 800px) {
    .technology__item {
      min-width: 84px;
      min-height: 57px;
      font-size: 12px;
      line-height: 15px;
    }
  }
  