  .movies-card-list__button-add-more-cards {
    width: 320px;
    min-height: 36px;
    margin: 50px auto 80px;
    border: none;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    background-color: #2F2F2F;
    color: #fff;
    border-radius: 10px;
    transition: opacity.5s, transform 0.5s, background-color 0.5s;
  }
  
  .movies-card-list__button-add-more-cards:hover {
    cursor: pointer;
    opacity: 0.7;
    transform: scale(1.075);
    background-color: rgb(238, 238, 238);
  }
  
  @media (max-width: 800px) {
    .movies-card-list__button-add-more-cards {
      margin: 50px auto 80px;
    }
  }
  
  @media (max-width: 400px) {
    .movies-card-list__button-add-more-cards {
      width: 240px;
      margin-bottom: 28px;
    }
  }
  