.about-me {
    padding: 110px 70px 100px;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    background-color: #202020;
  }
  
  @media (max-width: 800px) {
    .about-me {
      padding: 90px 50px 90px;
    }
  }
  
  @media (max-width: 400px) {
    .about-me {
      padding: 70px 14px;
    }
  }
  
  .about-me__wrapper {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    max-width: 600px;
    width: 53%;
  }
  
  @media (max-width: 700px) {
    .about-me__wrapper {
      width: unset;
      order: 1;
    }
  }
  
  .about-me__title {
    font-style: normal;
    font-weight: normal;
    font-size: 50px;
    line-height: 58px;
    letter-spacing: -0.04em;
    color: #fff;
    width: 100%;
    margin: 0;
  }
  
  @media (max-width: 800px) {
    .about-me__title {
      font-size: 40px;
      line-height: 40px;
    }
  }
  
  @media (max-width: 400px) {
    .about-me__title {
      font-size: 30px;
      line-height: 36px;
    }
  }
  
  .about-me__subtitle {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    color: #fff;
    mix-blend-mode: normal;
    width: 100%;
    margin: 18px 0 26px;
  }
  
  @media (max-width: 800px) {
    .about-me__subtitle {
      font-size: 12px;
      line-height: 18px;
      margin: 16px 0 20px;
    }
  }
  
  @media (max-width: 400px) {
    .about-me__subtitle {
      font-size: 11px;
      line-height: 16px;
      margin: 20px 0;
    }
  }
  
  .about-me__text {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: #fff;
    mix-blend-mode: normal;
    max-width: 600px;
    margin: 0 0 34px;
    display: -webkit-box;
    -webkit-line-clamp: 8;
    -webkit-box-orient: vertical;
    overflow-y: scroll;
    overflow-x: unset;
    padding-right: 10px;
  }
  
  .about-me__text::-webkit-scrollbar {
    width: 3px;
  }
  
  .about-me__text::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #e8e6e6;
  }
  
  @media (max-width: 800px) {
    .about-me__text {
      font-size: 12px;
      line-height: 18px;
      -webkit-line-clamp: 8;
      margin-bottom: 51px;
    }
  }
  
  @media (max-width: 600px) {
    .about-me__text {
      display: -webkit-box;
      margin-bottom: 23px;
      padding-right: 10px;
    }
  }
  
  @media (max-width: 400px) {
    .about-me__text {
      font-size: 11px;
      line-height: 16px;
    }
  }
  
  .about-me__photo {
    max-height: 327px;
    border-radius: 10px;
    min-width: 270px;
    object-fit: cover;
    margin-left: auto;
  }
  
  @media (max-width: 800px) {
    .about-me__photo {
      max-height: 307px;
      min-width: 255px;
    }
  }
  
  @media (max-width: 700px) {
    .about-me__photo {
      margin: 0 auto 40px;
      max-height: 352px;
      min-width: 289px;
    }
  }
  