.popup {
    opacity: 0;
    visibility: hidden;
    transition: visibility 1s, opacity 1s;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  
  .popup:hover {
    cursor: pointer;
  }
  
  .popup_type_error-message {
    z-index: 3;
  }
  
  .visible-block {
    opacity: 1;
    visibility: visible;
  }
  
  .popup__container {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    min-width: 430px;
    min-height: 330px;
    background-color: #fff;
    z-index: 2;
    border-radius: 10px;
    padding: 0 36px 0;
    box-sizing: border-box;
  }
  
  .popup__container:hover {
    cursor: auto;
  }
  
  @media screen and (max-width: 570px) {
    .popup__container {
      min-width: 282px;
      min-height: 322px;
      padding: 0 22px 0;
    }
  }
  
  .popup__container_type_error-message {
    min-height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 600px;
  }
  
  .popup__close {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    position: absolute;
    width: 40px;
    height: 40px;
    background: url(../../images/popup-close.svg) transparent no-repeat center
      center/140%;
    right: -40px;
    top: -40px;
    transition: opacity 0.5s, transform 0.5s;
  }
  
  .popup__close:focus {
    outline: none;
  }
  
  .popup__close:hover {
    opacity: 0.6;
    transform: scale(1.1);
    cursor: pointer;
  }
  
  @media screen and (max-width: 570px) {
    .popup__close {
      top: -45px;
      right: 5px;
      width: 26px;
      height: 26px;
    }
  }
  
  .popup__title {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    margin: 50px 20px;
  }
  
  @media screen and (max-width: 570px) {
    .popup__title {
      font-size: 18px;
      line-height: 22px;
      margin: 20px 0;
    }
  }
  
  .popup__title_type_error-message {
    color: red;
    text-align: center;
  }
  